.container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));

  font-size: 100px;
  color: #fff;

  @media (min-width: 1200px) {
    grid-template-columns: repeat(3, minmax(300px, 1fr));
  }

  &__block {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 300px;

    @each $number, $color in $colors {
      &--#{$number} {
        background-color: #{$color};
      }
    }
  }
}
